import {
  Piece,
  Empty,
  Hisha0,
  Kin0,
  Fu0,
  Fu1,
  Kyou0,
  Kyou1,
  Kei0,
  Kei1,
} from '../../model/shogi/Piece'

/**
 * é§ãæã
 * canPromote ãéã£ã¦ããåæ
 * @param p Piece æãåã®é§
 */
export function promote(p: Piece): Piece {
  if (p > 0) return p + 10
  return p - 10
}

/**
 * é§ãæãåã®é§ã«ãã
 * @param p Piece æé§
 */
export function demote(p: Piece): Piece {
  if (Math.abs(p) < 10) return p

  if (p < 0) return p + 10
  return p - 10
}

export interface CanPromoteProps {
  // ç§»ååã®è¡
  sourceRow: number

  // ç§»åå¾ã®è¡
  destRow: number

  // ç§»åããé§
  piece: Piece
}

/**
 * æã»ä¸æã®é¸æãã§ãããã©ãã
 * é¸æå¯è½: true
 * é¸æä¸å¯: false
 * @param props CanPromoteProps
 * @returns boolean
 *          æããé§ && sourceRow ã destRow ãæµé£ãªã true
 *          æããªãé§(éã¨ããæ¢ã«æé§ã®å ´å)ãªã false
 */
export function canPromote(props: CanPromoteProps): boolean {
  // æã¡é§
  if (props.sourceRow === -1) return false

  const isPromotable: boolean =
    props.piece !== Empty &&
    Math.abs(props.piece) !== Kin0 &&
    Math.abs(props.piece) <= Hisha0

  const rows: number[] = props.piece > 0 ? [0, 1, 2] : [6, 7, 8]
  const isInEnemyArea: boolean =
    rows.includes(props.sourceRow) || rows.includes(props.destRow)

  return isPromotable && isInEnemyArea
}

/**
 * ç§»åå¾ã«ãå¿ãæãå¿è¦ãããã
 * æ­©ã»é¦ã»æ¡ãä¸æã®å ´åã«æ¬¡ã«åããªãã¨ã true
 * @param p Piece åããé§
 * @param row ç§»åå¾ã®è¡
 * @returns boolean
 *          æå¿è¦ãããå ´å true
 */
export function mustPromote(p: Piece, row: number): boolean {
  return (
    ((p === Fu0 || p === Kyou0) && row === 0) ||
    ((p === Fu1 || p === Kyou1) && row === 8) ||
    (p === Kei0 && (row === 0 || row === 1)) ||
    (p === Kei1 && (row === 8 || row === 7))
  )
}
