/**
 * points ã attacker ã®é§ã®å¹ãã«å¥ã£ã¦ããã
 * çææ¾ç½®ã»æã¡æ­©è©°ããã§ãã¯ã«ä½¿ç¨ãã
 * å¥ã£ã¦ãã: true
 * å¥ã£ã¦ããªã: false
 *
 * ä¾. åæå±é¢ã§åæã®59çã«ãå¾æã®é§ã®å¹ããããããèª¿ã¹ãã¨ã
 * pos ã¯åæå±é¢
 * point ã¯ã{ row: 8, column: 4 }
 * attacker ã¯ Gote(-1)
 * ãæ¸¡ããfalse ãè¿ã
 *
 * @param pos Position éç½®
 * @param point Point èª¿ã¹ããä½ç½®
 * @param attacker Turn æ»ãæçª
 * @param ignoreCheckLeaving boolean | undefined çææ¾ç½®ãã§ãã¯ãç¡è¦ããã
 *                           ç¡è¦ãã(ãã§ãã¯ããªã): true
 *                           ç¡è¦ããªã(ãã§ãã¯ãã): false | undefined
 *                           åºæ¬çã«å¤ããå¼ã¶æã¯ undefined ã§
 */
export default function isPointed(
  pos: Position,
  point: Point,
  attacker: Turn,
  ignoreCheckLeaving?: boolean
): boolean {
  for (let row = 0; row < pos.pos.length; row++) {
    for (let column = 0; column < pos.pos.length; column++) {
      const piece: Piece = pos.pos[row][column]
      // åãå´ã®é§ããç©ºãã¹ãªãã¹ã­ãã
      if (piece * attacker <= 0) continue

      // æ»ãå´ã®é§ã® targets ã« point ãå«ã¾ãã¦ããªãã£ããã¹ã­ãã
      const targets: Point[] = getTargets(pos, { row, column, piece })
      const foundIndex: number = find(targets, point)
      if (foundIndex === -1) continue

      // çææ¾ç½®ãã§ãã¯ãããªãã¨ãã
      // æ»ãå´ã®é§ãçãããªãã¨ãã
      // åãå´ã®é§ãçã®æã¯
      // æ¬¡ã«åããã®ã§ true
      if (
        ignoreCheckLeaving ||
        Math.abs(piece) !== Gyoku0 ||
        (point.piece && Math.abs(point.piece) === Gyoku0)
      )
        return true

      // æ»ãå´ã®é§ãçãªã®ã§åãããçµæãæ»ãå´ã®çãçææ¾ç½®ã«ãªããªããèª¿ã¹ã
      const moved = moveBoardOnly({
        pos,
        source: { row, column },
        dest: { row: point.row, column: point.column },
        piece,
      })
      if (isPointed(moved, point, -attacker as Turn, true)) continue
      return true
    }
  }

  return false
}
